<template>
  <el-row class="joke">
    <el-row class="text-center">
      <el-row v-for="item in jokes" :key="item.soureid">
        <el-row class="jokeText text-left line-h30">{{item.text}}</el-row>
        <video class="myVideo" v-if="item.type==='video'" :src="item.video" controls></video>
        <el-image class="p-10" v-if="item.type==='image'" :src="item.image" alt="">
          <div slot="placeholder" v-loading="true"></div>
          <div slot="error" class="image-slot">
            <img src="img/no_image.jpg" alt="" width="100%">
          </div>
        </el-image>
        <el-image class="p-10" v-if="item.type==='gif'" :src="item.gif" alt="">
          <div slot="placeholder" v-loading="true"></div>
          <div slot="error" class="image-slot">
            <img src="img/no_image.jpg" alt="" width="100%">
          </div>
        </el-image>
        <el-divider></el-divider>
      </el-row>
    </el-row>
    <el-row class="text-center">
      <el-row v-for="item in textJokes" :key="item.hashId">
        <el-row class="jokeText text-left line-h30">{{item.content}}</el-row>
        <el-divider></el-divider>
      </el-row>
    </el-row>
    <el-row class='getMore p-40' v-loading="true">
      <!--<el-button type="primary" @click="getData">刷新</el-button>-->
      <!--<el-button type="primary" @click="getRealData">最新段子</el-button>-->
    </el-row>
  </el-row>
</template>
<script>
import { getRandomJoke } from '@/api/apiopen'
import { getJoke } from '@/api/juhe'

export default {
  name: 'joke',
  data () {
    return {
      jokes: [],
      textJokes: []
    }
  },
  created () {
    // this.getJoke()
    this.getTextJoke()
  },
  mounted () {
    if (this.jokes.length !== 0) {
      this.$inView('.getMore').on('enter', () => {
        this.getJoke()
      }).on('exit', () => {
      })
    }
  },
  methods: {
    getJoke () {
      getRandomJoke({
        param: {}
      }).then(res => {
        if (res.code === 200) {
          this.jokes = res.data
        } else {
          this.$message.error(res)
        }
      }).catch(error => {
        console.log(error)
      })
    },
    getTextJoke () {
      getJoke({
        param: {}
      }).then(res => {
        console.log(res)
        if (res.error_code === 0) {
          this.textJokes = res.result.data
        } else {
          this.$message.error(res.reason)
        }
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>
<style lang="scss">
.joke {
  background-color: #fff;

  .jokeText {
    width: 50%;
    max-width: 400px;
    margin: 0 auto;
    padding: 20px 0;
  }

  .myVideo {
    width: 100%;
    max-width: 400px;
  }

  .el-image {
    width: 100%;
    max-width: 400px;

    .image-slot {
      height: 50px;
    }
  }
}


@media screen and (max-device-width: 400px) { //适应手机的css
  // 真实宽度小于400px (非web端窗口大小)

}
</style>
